#aboutme {
    background-color: var(--color-secondary);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 91.7vh;
  }
  
  .aboutme-container {
    width: 130vh;
    height: 70vh;
    background: rgb(255, 255, 255);
    border-radius: 0.4em;
    box-shadow: 0.3em 0.3em 0.7em #00000015;
    transition: border 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    border: rgb(250, 250, 250) 0.2em solid;
    padding: 30px 30px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .aboutme-text-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: auto;
    max-width: 60%;
    text-align: center;
  }
  
  .aboutme-img {
    width: 40vh;
    height: auto;
    transition: 0.7s;
  }
  
  .aboutme-img:hover {
    transform: scale(1.05);
    transform: rotate(30);
  }
  
  .aboutme-button-container {
    margin: 30px 0;
  }
  
  .aboutme-button {
    position: relative;
    border: none;
    background: transparent;
    padding: 0;
    cursor: pointer;
    outline-offset: 4px;
    transition: filter 250ms;
    user-select: none;
    touch-action: manipulation;
  }
  
  .shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: hsl(0deg 0% 0% / 0.25);
    will-change: transform;
    transform: translateY(2px);
    transition: transform 600ms cubic-bezier(0.3, 0.7, 0.4, 1);
  }
  
  .edge {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: linear-gradient(
      to left,
      hsl(340deg 100% 16%) 0%,
      hsl(340deg 100% 32%) 8%,
      hsl(340deg 100% 32%) 92%,
      hsl(340deg 100% 16%) 100%
    );
  }
  
  .front {
    display: block;
    position: relative;
    padding: 12px 27px;
    border-radius: 12px;
    font-size: 1.1rem;
    color: white;
    background: hsl(345deg 100% 47%);
    will-change: transform;
    transform: translateY(-4px);
    transition: transform 600ms cubic-bezier(0.3, 0.7, 0.4, 1);
  }
  
  .aboutme-button:hover {
    filter: brightness(110%);
  }
  
  .aboutme-button:hover .front {
    transform: translateY(-6px);
    transition: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);
  }
  
  .aboutme-button:active .front {
    transform: translateY(-2px);
    transition: transform 34ms;
  }
  
  .aboutme-button:hover .shadow {
    transform: translateY(4px);
    transition: transform 250ms cubic-bezier(0.3, 0.7, 0.4, 1.5);
  }
  
  .aboutme-button:active .shadow {
    transform: translateY(1px);
    transition: transform 34ms;
  }
  
  .aboutme-button:focus:not(:focus-visible) {
    outline: none;
  }
  
  .aboutme-icons-container {
    margin: 10px 0;
  }
  
  .icon {
    width: 40px;
    opacity: 0.3;
    margin-right: 15px;
  }
  
  .icon:hover {
    opacity: 1;
    transition: 1s;
  }
  
  @media (max-width: 575.98px) {
    .aboutme-container {
      height: auto;
    }
  }
  
  @media (max-width: 768px) {
    .aboutme-text-container {
      max-width: 100%;
    }
    .aboutme-img {
      display: none;
    }
  }
  
  @media (max-width: 992px) {
    .aboutme-text-container {
      max-width: 100%;
    }
    .aboutme-img {
      display: none;
    }
  }
  
  @media (max-width: 1199.98px) {
    .aboutme-text-container {
      max-width: 100%;
    }
    .aboutme-img {
      display: none;
    }
  }